"use client";
import { useState } from "react";
import { TrendingUp, CheckCircle, XCircle, Database, Table } from "lucide-react";

type NotificationType = {
  type: "success" | "error";
  message: string;
};

const API_URL = "http://localhost:8080/api";

export default function Home() {
  const [buyerPrice, setBuyerPrice] = useState("");
  const [buyerQuantity, setBuyerQuantity] = useState("");
  const [buyerDate, setBuyerDate] = useState("");
  const [buyerTime, setBuyerTime] = useState("");
  const [buyerCurrencyType, setBuyerCurrencyType] = useState("");

  const [sellerPrice, setSellerPrice] = useState("");
  const [sellerQuantity, setSellerQuantity] = useState("");
  const [sellerDate, setSellerDate] = useState("");
  const [sellerTime, setSellerTime] = useState("");
  const [sellerCurrencyType, setSellerCurrencyType] = useState("");

  const [notification, setNotification] = useState<NotificationType | null>(null);

  const showNotification = (type: "success" | "error", message: string) => {
    setNotification({ type, message });
    setTimeout(() => setNotification(null), 4000);
  };

  const handleBuySubmit = async () => {
    const orderData = {
      role: "buyer",
      price: Number(buyerPrice),
      quantity: Number(buyerQuantity),
      trade_date: buyerDate,
      trade_time: buyerTime,
      currency_type: buyerCurrencyType,
    };
    
    try {
      const response = await fetch(`${API_URL}/orders`, {
        method: 'POST',
        headers: {
          'Content-Type': 'application/json',
        },
        body: JSON.stringify(orderData),
      });
      
      if (response.ok) {
        const data = await response.json();
        console.log("Buy Order Created:", data);
        showNotification("success", `Buy order placed! ID: ${data.id} | Currency: ${buyerCurrencyType}`);
        
        setBuyerPrice("");
        setBuyerQuantity("");
        setBuyerDate("");
        setBuyerTime("");
        setBuyerCurrencyType("");
      } else {
        const error = await response.text();
        showNotification("error", `Failed to place buy order: ${error}`);
      }
    } catch (error) {
      console.error("Error:", error);
      showNotification("error", "Network error. Please check if backend is running.");
    }
  };

  const handleSellSubmit = async () => {
    const orderData = {
      role: "seller",
      price: Number(sellerPrice),
      quantity: Number(sellerQuantity),
      trade_date: sellerDate,
      trade_time: sellerTime,
      currency_type: sellerCurrencyType,
    };
    
    try {
      const response = await fetch(`${API_URL}/orders`, {
        method: 'POST',
        headers: {
          'Content-Type': 'application/json',
        },
        body: JSON.stringify(orderData),
      });
      
      if (response.ok) {
        const data = await response.json();
        console.log("Sell Order Created:", data);
        showNotification("success", `Sell order placed! ID: ${data.id} | Currency: ${sellerCurrencyType}`);
        
        setSellerPrice("");
        setSellerQuantity("");
        setSellerDate("");
        setSellerTime("");
        setSellerCurrencyType("");
      } else {
        const error = await response.text();
        showNotification("error", `Failed to place sell order: ${error}`);
      }
    } catch (error) {
      console.error("Error:", error);
      showNotification("error", "Network error. Please check if backend is running.");
    }
  };

  const viewTable = (table: string) => {
    // Open the table view in a new tab
    window.open(`/view-table?table=${table}`, '_blank');
  };

  return (
    <div className="min-h-screen bg-gray-50 py-12 px-4 sm:px-6 lg:px-8">
      {notification && (
        <div className="fixed top-4 right-4 z-50 transition-all duration-300 ease-out">
          <div className="flex items-center gap-3 px-6 py-4 rounded-lg shadow-2xl border-2 bg-white border-gray-300">
            {notification.type === "success" ? (
              <CheckCircle className="text-black flex-shrink-0" size={24} />
            ) : (
              <XCircle className="text-black flex-shrink-0" size={24} />
            )}
            <div className="max-w-xs">
              <p className="font-semibold text-black">
                {notification.type === "success" ? "Success!" : "Error"}
              </p>
              <p className="text-sm text-gray-600">
                {notification.message}
              </p>
            </div>
          </div>
        </div>
      )}

      <div className="max-w-7xl mx-auto">
        <div className="text-center mb-8">
          <div className="flex items-center justify-center gap-3 mb-2">
            <TrendingUp className="text-black" size={40} />
            <h1 className="text-4xl font-bold text-black">Trade Platform</h1>
          </div>
          <p className="text-gray-600">Place your buy or sell orders</p>
        </div>

        <div className="grid grid-cols-1 lg:grid-cols-2 gap-8">
          <div className="bg-white rounded-3xl shadow-lg overflow-hidden border-2 border-black">
            <div className="px-6 py-5">
              <h2 className="text-2xl font-bold text-black">Buyers</h2>
            </div>

            <div className="px-6 py-6 space-y-5">
              <div>
                <label className="block text-sm font-semibold text-black mb-2">
                  Buying Price
                </label>
                <div className="relative">
                  <input
                    type="number"
                    value={buyerPrice}
                    onChange={(e) => setBuyerPrice(e.target.value)}
                    step="0.01"
                    placeholder="e.g. 123.45"
                    className="w-full px-4 py-3 border-2 border-gray-300 rounded-lg focus:ring-2 focus:ring-black focus:border-black transition-all outline-none bg-white text-black placeholder-gray-400"
                  />
                </div>
              </div>

              <div>
                <label className="block text-sm font-semibold text-black mb-2">
                  Quantity
                </label>
                <div className="relative">
                  <input
                    type="number"
                    value={buyerQuantity}
                    onChange={(e) => setBuyerQuantity(e.target.value)}
                    placeholder="e.g. 10"
                    className="w-full px-4 py-3 border-2 border-gray-300 rounded-lg focus:ring-2 focus:ring-black focus:border-black transition-all outline-none bg-white text-black placeholder-gray-400"
                  />
                </div>
              </div>

              <div>
                <label className="block text-sm font-semibold text-black mb-3">
                  Currency Type
                </label>
                <div className="flex gap-6">
                  <label className="flex items-center gap-2 cursor-pointer">
                    <input
                      type="checkbox"
                      checked={buyerCurrencyType === "cash"}
                      onChange={() => setBuyerCurrencyType("cash")}
                      className="w-5 h-5 border-2 border-gray-300 rounded focus:ring-2 focus:ring-black cursor-pointer"
                    />
                    <span className="text-black font-medium">Cash</span>
                  </label>
                  <label className="flex items-center gap-2 cursor-pointer">
                    <input
                      type="checkbox"
                      checked={buyerCurrencyType === "crypto"}
                      onChange={() => setBuyerCurrencyType("crypto")}
                      className="w-5 h-5 border-2 border-gray-300 rounded focus:ring-2 focus:ring-black cursor-pointer"
                    />
                    <span className="text-black font-medium">Cryptocurrency</span>
                  </label>
                </div>
              </div>

              <div className="grid grid-cols-2 gap-4">
                <div>
                  <label className="block text-sm font-semibold text-black mb-2">
                    Date
                  </label>
                  <div className="relative">
                    <input
                      type="date"
                      value={buyerDate}
                      onChange={(e) => setBuyerDate(e.target.value)}
                      className="w-full px-3 py-3 border-2 border-gray-300 rounded-lg focus:ring-2 focus:ring-black focus:border-black transition-all outline-none text-sm bg-white text-black"
                    />
                  </div>
                </div>

                <div>
                  <label className="block text-sm font-semibold text-black mb-2">
                    Time
                  </label>
                  <div className="relative">
                    <input
                      type="time"
                      value={buyerTime}
                      onChange={(e) => setBuyerTime(e.target.value)}
                      step="1"
                      className="w-full px-3 py-3 border-2 border-gray-300 rounded-lg focus:ring-2 focus:ring-black focus:border-black transition-all outline-none text-sm bg-white text-black"
                    />
                  </div>
                </div>
              </div>

              <button
                onClick={handleBuySubmit}
                disabled={!buyerPrice || !buyerQuantity || !buyerDate || !buyerTime || !buyerCurrencyType}
                className={`w-full font-bold py-4 px-6 rounded-lg border-2 transform hover:scale-105 transition-all duration-200 shadow-lg hover:shadow-xl disabled:cursor-not-allowed disabled:transform-none flex items-center justify-center gap-2 ${
                  buyerPrice && buyerQuantity && buyerDate && buyerTime && buyerCurrencyType
                    ? "bg-black text-white border-black hover:bg-gray-800"
                    : "bg-white text-black border-black opacity-50"
                }`}
              >
                Place Buy
              </button>
            </div>
          </div>

          <div className="bg-white rounded-3xl shadow-lg overflow-hidden border-2 border-black">
            <div className="px-6 py-5">
              <h2 className="text-2xl font-bold text-black">Sellers</h2>
            </div>

            <div className="px-6 py-6 space-y-5">
              <div>
                <label className="block text-sm font-semibold text-black mb-2">
                  Selling Price
                </label>
                <div className="relative">
                  <input
                    type="number"
                    value={sellerPrice}
                    onChange={(e) => setSellerPrice(e.target.value)}
                    step="0.01"
                    placeholder="e.g. 125.00"
                    className="w-full px-4 py-3 border-2 border-gray-300 rounded-lg focus:ring-2 focus:ring-black focus:border-black transition-all outline-none bg-white text-black placeholder-gray-400"
                  />
                </div>
              </div>

              <div>
                <label className="block text-sm font-semibold text-black mb-2">
                  Quantity
                </label>
                <div className="relative">
                  <input
                    type="number"
                    value={sellerQuantity}
                    onChange={(e) => setSellerQuantity(e.target.value)}
                    placeholder="e.g. 5"
                    className="w-full px-4 py-3 border-2 border-gray-300 rounded-lg focus:ring-2 focus:ring-black focus:border-black transition-all outline-none bg-white text-black placeholder-gray-400"
                  />
                </div>
              </div>

              <div>
                <label className="block text-sm font-semibold text-black mb-3">
                  Currency Type
                </label>
                <div className="flex gap-6">
                  <label className="flex items-center gap-2 cursor-pointer">
                    <input
                      type="checkbox"
                      checked={sellerCurrencyType === "cash"}
                      onChange={() => setSellerCurrencyType("cash")}
                      className="w-5 h-5 border-2 border-gray-300 rounded focus:ring-2 focus:ring-black cursor-pointer"
                    />
                    <span className="text-black font-medium">Cash</span>
                  </label>
                  <label className="flex items-center gap-2 cursor-pointer">
                    <input
                      type="checkbox"
                      checked={sellerCurrencyType === "crypto"}
                      onChange={() => setSellerCurrencyType("crypto")}
                      className="w-5 h-5 border-2 border-gray-300 rounded focus:ring-2 focus:ring-black cursor-pointer"
                    />
                    <span className="text-black font-medium">Cryptocurrency</span>
                  </label>
                </div>
              </div>

              <div className="grid grid-cols-2 gap-4">
                <div>
                  <label className="block text-sm font-semibold text-black mb-2">
                    Date
                  </label>
                  <div className="relative">
                    <input
                      type="date"
                      value={sellerDate}
                      onChange={(e) => setSellerDate(e.target.value)}
                      className="w-full px-3 py-3 border-2 border-gray-300 rounded-lg focus:ring-2 focus:ring-black focus:border-black transition-all outline-none text-sm bg-white text-black"
                    />
                  </div>
                </div>

                <div>
                  <label className="block text-sm font-semibold text-black mb-2">
                    Time
                  </label>
                  <div className="relative">
                    <input
                      type="time"
                      value={sellerTime}
                      onChange={(e) => setSellerTime(e.target.value)}
                      step="1"
                      className="w-full px-3 py-3 border-2 border-gray-300 rounded-lg focus:ring-2 focus:ring-black focus:border-black transition-all outline-none text-sm bg-white text-black"
                    />
                  </div>
                </div>
              </div>

              <button
                onClick={handleSellSubmit}
                disabled={!sellerPrice || !sellerQuantity || !sellerDate || !sellerTime || !sellerCurrencyType}
                className={`w-full font-bold py-4 px-6 rounded-lg border-2 transform hover:scale-105 transition-all duration-200 shadow-lg hover:shadow-xl disabled:cursor-not-allowed disabled:transform-none flex items-center justify-center gap-2 ${
                  sellerPrice && sellerQuantity && sellerDate && sellerTime && sellerCurrencyType
                    ? "bg-black text-white border-black hover:bg-gray-800"
                    : "bg-white text-black border-black opacity-50"
                }`}
              >
                Place Sell
              </button>
            </div>
          </div>
        </div>

        <div className="mt-12 text-center">
          <h2 className="text-2xl font-bold text-black mb-6 flex items-center justify-center gap-3">
            <Database className="text-black" size={32} />
            View Database Tables (Real-Time)
          </h2>
          <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-5 gap-6 max-w-6xl mx-auto">
            <div 
              onClick={() => viewTable("buyer_cash")}
              className="group bg-white rounded-3xl shadow-lg overflow-hidden border-2 border-black p-6 hover:shadow-xl transition-all duration-200 flex flex-col items-center text-center cursor-pointer hover:scale-105"
            >
              <Table className="text-blue-600 group-hover:text-blue-700 mb-3" size={48} />
              <h3 className="text-xl font-bold text-black mb-2">Buyer Cash</h3>
              <p className="text-gray-600 text-sm">View buyer cash orders</p>
            </div>
            <div 
              onClick={() => viewTable("buyer_crypto")}
              className="group bg-white rounded-3xl shadow-lg overflow-hidden border-2 border-black p-6 hover:shadow-xl transition-all duration-200 flex flex-col items-center text-center cursor-pointer hover:scale-105"
            >
              <Table className="text-green-600 group-hover:text-green-700 mb-3" size={48} />
              <h3 className="text-xl font-bold text-black mb-2">Buyer Crypto</h3>
              <p className="text-gray-600 text-sm">View buyer crypto orders</p>
            </div>
            <div 
              onClick={() => viewTable("seller_cash")}
              className="group bg-white rounded-3xl shadow-lg overflow-hidden border-2 border-black p-6 hover:shadow-xl transition-all duration-200 flex flex-col items-center text-center cursor-pointer hover:scale-105"
            >
              <Table className="text-purple-600 group-hover:text-purple-700 mb-3" size={48} />
              <h3 className="text-xl font-bold text-black mb-2">Seller Cash</h3>
              <p className="text-gray-600 text-sm">View seller cash orders</p>
            </div>
            <div 
              onClick={() => viewTable("seller_crypto")}
              className="group bg-white rounded-3xl shadow-lg overflow-hidden border-2 border-black p-6 hover:shadow-xl transition-all duration-200 flex flex-col items-center text-center cursor-pointer hover:scale-105"
            >
              <Table className="text-orange-600 group-hover:text-orange-700 mb-3" size={48} />
              <h3 className="text-xl font-bold text-black mb-2">Seller Crypto</h3>
              <p className="text-gray-600 text-sm">View seller crypto orders</p>
            </div>
            <div 
              onClick={() => viewTable("all")}
              className="group bg-white rounded-3xl shadow-lg overflow-hidden border-2 border-black p-6 hover:shadow-xl transition-all duration-200 flex flex-col items-center text-center cursor-pointer hover:scale-105"
            >
              <Database className="text-red-600 group-hover:text-red-700 mb-3" size={48} />
              <h3 className="text-xl font-bold text-black mb-2">All Orders</h3>
              <p className="text-gray-600 text-sm">View all tables data</p>
            </div>
          </div>
        </div>

        <p className="text-center text-gray-600 text-sm mt-8">
          All fields including currency type are required to submit a trade. Click table cards to view data in a new tab.
        </p>
      </div>
    </div>
  );
}